.dropdown {
  border: 2px solid black;
  text-align: center;
  margin: 20px;
  padding: 5px;
  cursor: pointer;
  position: relative;
}

.dropdownlist{
  border: 2px solid black;
  text-align: center;
  /* margin: 20px;
  padding: 5px; */
  cursor: pointer;
  position: absolute;
  top: 3px;
  left: 0px;
  right: 0px;
  z-index: 5;
  background-color: wheat;
}

.button {
  border: 2px solid black;
  text-align: center;
  margin: 20px;
  padding: 5px;
  cursor: pointer;
}

.infoBox {
  border: 2px solid black;
  text-align: center;
  margin: 5px;
  padding: 20px;
}

.infoBoxSmall {
  border: 2px solid black;
  text-align: center;
  margin: 20px;
  padding: 5px;
}

.title {
  text-align: center;
  font-weight: 900
}

select {
  appearance: none;
  outline: 0;
  background: green;
  background-image: none;
  width: 100%;
  height: 100%;
  color: black;
  cursor: pointer;
  border: 1px solid black;
  border-radius: 3px;
}

.select {
  position: relative;
  display: block;
  width: 15em;
  height: 2em;
  line-height: 3;
  overflow: hidden;
  border-radius: .25em;
  padding-bottom: 10px;
}

ul {
  list-style-type: none;
  padding: 0px;
  margin: 0px;
 }

 li:hover {
  background-color: white;
 }